@import 'src/common/sass/variables';
@import 'src/common/sass/mixins';

.homeBlock {
   height: 100vh;
   background-color: $bgColor;
   background-attachment: fixed;
   background-repeat: no-repeat;
   background-position: 50% 50%;
   position: relative;
   -webkit-background-size: cover;
   -moz-background-size: cover;
   -o-background-size: cover;
   background-size: cover;
   overflow: hidden;
   padding: 130px 0;

   .container {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: space-between;
      @include globalContainer(55%);

      .greeting {
         width: 500px;

         span {
            color: $titleColor;
            font-size: 25px;
         }

         h1 {
            font-size: 62px;
            color: $titleColor;
            font-weight: 700;
            letter-spacing: 1px;
            margin: 0 0 11px;
            line-height: 76px;

            span {
               font-size: 62px;
               color: $titleColor;
               font-weight: 700;
               letter-spacing: 1px;
               margin: 0 0 11px;
               line-height: 76px;
               position: relative;
               z-index: 1;

               &::after {
                  content: "";
                  position: absolute;
                  left: 0;
                  right: 0;
                  top: 61px;
                  height: 13px;
                  width: 100%;
                  z-index: -1;
                  background: $primaryColor;
               }
            }
         }

         p {
            color: $titleSecondaryColor;
            font-size: 32px;
            font-weight: 400;
            letter-spacing: .3px;
            margin: 0;
         }
      }

      .borderPhoto {
         width: 350px;
         height: 450px;
         position: relative;

         border: 5px solid $primaryColor;

         .mainPhoto {
            width: 100%;
            height: 90%;
            position: absolute;
            top: 17px;
            left: -20px;
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;

         }
      }

      /*@media screen and (max-width: 1003px) {
         flex-direction: column;
      }*/
   }

   .background {
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      height: 100%;
      width: 100%;
      z-index: 0;
      opacity: 0.8;
   }
}

