@media screen and (max-width: 550px) {
   .homeBlock {
      height: 1010px;
      line-height: 3em;

      .container {
         .greeting {
            margin-bottom: 40px;
            font-size: 15px;
            span {
               font-size: 19px !important;
            }

            h1 {
               font-size: 32px !important;
               line-height: 1em !important;

               span {
                  font-size: 32px !important;

                  &::after {
                     top: 31px !important;
                     height: 9px !important;
                  }
               }
            }

            p {
               font-size: 21px !important;
            }
         }
      }
   }
}

@media screen and (max-width: 1546px) {
   .container {
      .greeting {
         margin-bottom: 50px;
      }
   }
}




